body {
  z-index: 0;
  background-color: #ffe8de;
}

#canvas {
  position: absolute;
  top: 50%;
  left: 20%;
  transform: translate(-50%, -50%);
}

#video {
  z-index: 0;
  border: 3px solid #e7717d;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#chart-container {
  width: 20%;
  height: 25%;
  margin-left: 1200px;
  position: absolute;
  top: 50%;
}

.container {
  flex-wrap: wrap;
  margin: 0 auto;
  display: flex;
}

.leftbox {
  float: left;
  width: 20%;
  height: 100vh;
  background-color: #edc7b7;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  bottom: 10px;
  box-shadow: 0 1px 2px #3b3b3b30, 0 1px 2px #3b3b3b30;
}

.topbox {
  float: top;
  width: 100%;
  height: 10vh;
  background-color: #e7717d;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  box-shadow: 0 1px 2px #3b3b3b30, 0 1px 2px #3b3b3b30;
}

h1 {
  color: #fff;
  font-family: monospace;
  font-size: 50px;
  font-weight: bold;
}

h2 {
  color: #ac3b61;
  text-align: center;
  font-family: serif;
  font-size: xx-large;
}

ul {
  color: #ac3b61;
  font-family: serif;
}

#emotion, p {
  color: #ac3b61;
  text-align: center;
  font-family: serif;
}

.grid {
  display: grid;
}

.subgrid {
  grid-template-columns: subgrid;
  grid-gap: 16px;
  z-index: 0;
  justify-content: center;
  align-items: center;
  display: grid;
}

button {
  text-align: center;
  color: #fff;
  width: 85%;
  cursor: pointer;
  z-index: 3;
  background-color: #e7717d;
  border: none;
  border-radius: 8px;
  justify-content: center;
  place-self: center;
  align-items: center;
  padding: 10px 50px;
  font-family: serif;
  font-size: x-large;
  display: block;
  box-shadow: 0 1px 2px #3b3b3b30, 0 1px 2px #3b3b3b30;
}

#clock {
  color: #e7717d;
  justify-content: center;
  align-items: center;
  font-family: serif;
  font-size: 40px;
  font-weight: bold;
  display: inline-block;
}

#timeBox {
  color: #fff;
  text-align: center;
  background-color: #edc7b7;
  border: 2px solid #e7717d;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin-left: 30%;
  padding: 20px 60px;
  font-family: serif;
  font-size: x-large;
  text-decoration: none;
  display: inline-block;
  position: absolute;
  top: 35%;
  left: 45%;
  box-shadow: 0 2px 2px #00000040;
}

.popup .overlay {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background: #000000b3;
  display: none;
  position: fixed;
  top: 0;
}

.popup .content {
  width: 450px;
  height: 500px;
  z-index: 2;
  box-sizing: border-box;
  background: #fff;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(0);
}

.popup .close-btn {
  cursor: pointer;
  width: 30px;
  height: 30px;
  color: #fff;
  text-align: center;
  background: #222;
  border-radius: 50%;
  font-size: 25px;
  font-weight: 600;
  line-height: 30px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.popup.active .overlay {
  display: block;
}

.popup.active .content {
  transition: all .3s ease-in-out;
  transform: translate(-50%, -50%)scale(1);
}

@media only screen and (max-width: 1440px) {
  #canvas, #video {
    top: 60%;
    left: 45%;
  }

  #chart-container {
    width: 30%;
    height: 50%;
    margin-left: 67%;
    position: absolute;
    top: 55%;
  }
}

@media only screen and (min-width: 2000px) {
  #canvas, #video {
    top: 50%;
    left: 55%;
  }

  #chart-container {
    width: 30%;
    height: 40%;
    margin-left: 67%;
    position: absolute;
    top: 50%;
  }
}

.splash {
  width: 100%;
  height: 100vh;
  z-index: 4;
  color: #db3535;
  text-align: center;
  z-index: 0;
  background: #13080840;
  line-height: 90vh;
  position: fixed;
  top: 0;
  left: 0;
}

.splash.display-none {
  opacity: 0;
  width: 100%;
  height: 100vh;
  z-index: 4;
  color: #ca2424;
  text-align: center;
  z-index: 0;
  background: #13080840;
  line-height: 90vh;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.fade-in {
  opacity: 1;
  animation: fadeIn 1s ease-in fowards;
}

